#navbar {
  padding-left: 25px;
  font-family: "Anton";
  font-weight: "bold";
}
.a {
  color: #00796b;
}
.a:hover {
  color: #a7ffeb;
}
.h2-style {
  color: #00796b;

  font-family: "Anton", sans-serif;
}

.projects-grid {
  display: -ms-flexbox;
  display: flex;
}
.avatar-img {
  height: 200px;
  padding-top: 2em;
}
.card-panel {
  border-radius: 15px;
}
.card-panel h1 {
  font-size: 50px;
  font-weight: bold;
  color: #ffffff;
  -webkit-transition: all 3s ease-in-out;
  -o-transition: all 3s ease-in-out;
  transition: all 3s ease-in-out;
}
.card-panel h1:hover {
  -webkit-transform: scale(1.5);
      -ms-transform: scale(1.5);
          transform: scale(1.5);
  color: #263238;
}
.card-panel hr {
  border-top: 5px dotted ad1457;
  margin: auto;
}
.card-panel p {
  color: #ffffff;
  font-size: 20px;
  padding: 1em;
}
.icon-color {
  color: #37474f;
  -webkit-transition: all 3s ease-in-out;
  -o-transition: all 3s ease-in-out;
  transition: all 3s ease-in-out;
}
.icon-color:hover {
  -webkit-transform: scale(2);
      -ms-transform: scale(2);
          transform: scale(2);
  color: #263238;
}
/* contact page css */

.contact-grid p {
  font-family: "Marmelad", sans-serif;
  font-weight: bold;
  font-size: 15px;
}
.contact-list {
  margin: auto;
}
.contact-list ul ol {
  font-size: 1.2em;
  font-family: "Anton", "sans-serif";
  width: 90%;
  height: 120px;
  color: #00796b;
}
.contact-list ul ol span {
  padding-left: 3em;
  padding-top: 3em;
}
/* contact page css */
/* Resume Page css */
.resume {
  color: #00796b;
  font-size: 12px;
  font-family: "Marmelad";
}
.exp {
  margin-left: 1em;
  margin-top: auto;
  border-radius: 15px;
}
.edu {
  border-radius: 15px;
}



